import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ClientComponent } from './client.component';
import { PermissionsAuthGuard } from '@xenon/auth';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },
  {
    path: '',
    component: ClientComponent,
    children: [
      {
        path: 'home',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./home/home.module').then((m) => m.HomeModule),
      },
      {
        path: 'tasks',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./tasks/tasks.module').then((m) => m.TasksModule),
      },
      {
        path: 'drive',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./drive/drive.module').then((m) => m.DriveModule),
      },
      {
        path: 'team',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./team/team.module').then((m) => m.TeamModule),
      },
      {
        path: 'forms',
        canActivate: [PermissionsAuthGuard],
        loadChildren: () => import('./forms/xenon-forms.module').then((m) => m.XenonFormsModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ClientRoutingModule {}
